.App {
  text-align: center;
} 

#root>h2{
  text-align: center;
}

body{ 
  background: rgba(245,235,246,0.8) ;
}
ul{ list-style-type:none; }


.day>div:nth-child(7n){ 
  color:blue;
}

.day>div:nth-child(7n+1){ 
  color:red;
} 


.container{ 
  padding:20px; 
  background:#DCE8C3;
}

.calendar>.header button{ 
  border-radius: 50%;
  width:60px; 
  height:60px; 
  font-weight:bold; 
}

.day>div{ 
  font-weight:bold;
}

.todo{
  padding-top: 40px;  
  margin-bottom:60px; 
}

.todo>h3{
  font-weight: bold;
  font-size:24px;
}
.todo>form>input{
  border:0;
  background:transparent; 
  border-bottom:1px dashed #ccc;

  font-size:20px; 
}
.todo>form>button{
  border-radius: 20px;
  font-weight:bold; 
  background:#c96; 

}
.todo>ul>li>button{ 
  font-weight:bold; 
  border-radius: 30px;

}


#root>div{
  width:800px; 
  margin:0 auto;
}

#root>div>nav{
  width:800px; 
  margin:0 auto;
  text-align: center;
}



@keyframes slide {
  0% { 
    margin-left:100%;
  }
  100% { 
    margin-left:0%;
  }
}